@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,700,900&display=swap);
:root {
  --clr-blue: #002f4b;
  --clr-blue-light: #72afd0;
  --clr-neutral: #c7ced3;
  --clr-white: #f4f5f6;
  --clr-white-a: #ffffffd4;
  --clr-black-a: #0606068a;
  --border-radius-main: 25px;
  --border-radius-main-l: 25px 0 0 25px;
  --border-radius-main-r: 0 25px 25px 0;
  --border-radius-secondary: 15px;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  display: grid;
  place-items: center;

  height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
}

h2 {
  font-size: 1.8rem;
}

button {
  padding: 0.4rem 0.8rem;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  color: #f4f5f6;
  color: var(--clr-white);
  background-color: #002f4b;
  background-color: var(--clr-blue);
  border-radius: 25px;
  border-radius: var(--border-radius-main);
}

button:hover {
  color: #002f4b;
  color: var(--clr-blue);
  background-color: #72afd0;
  background-color: var(--clr-blue-light);
}
.forecast-wrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;

  padding: 2rem;
  background-color: var(--clr-neutral);
}

@media screen and (min-width: 600px) {
  .app {
    margin: 2rem;
  }

  .forecast-wrapper {
    flex-direction: row;
    grid-gap: 0;
    gap: 0;

    padding: 0;
    border-radius: var(--border-radius-main);
  }
}

.search-box {
  padding: 1rem 2.5rem;
  -webkit-margin-after: 0.5rem;
          margin-block-end: 0.5rem;
  background-color: var(--clr-neutral);
}

.search-bar {
  display: flex;
}

input[type = text] {
  width: 100%;
  padding: 0.4rem 0.8rem;
  border: none;
  font-size: 0.9rem;
  border-radius: var(--border-radius-main-l);
}

.search-bar button {
  width: 7rem;
  border-radius: var(--border-radius-main-r);
}

@media screen and (min-width: 600px) {
  .search-box {
    -webkit-margin-after: 1rem;
            margin-block-end: 1rem;
    border-radius: var(--border-radius-main);
  }

  .search-bar button {
    width: 9rem;
  }
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 100%;
  padding: 2rem;
  color: var(--clr-white);
  border-radius: var(--border-radius-main);
  background-color: var(--clr-blue);
  background-image:
    linear-gradient(to bottom, rgba(0, 18, 29, 0.95), rgba(45, 69, 82, 0.7)),
    url('https://source.unsplash.com/xfngap_DToE');
  background-position: center;
  background-size: cover;
}

.card-icon {
  width: 120px;
  aspect-ratio: 1 / 1;
}

@media screen and (min-width: 600px) {
  .card {
    width: 32ch;
  }
  .card p {
    font-size: 20px;
  }
  .card-icon {
    width: 100px;
  }
}

.item {
  display: flex;
  justify-content: space-between;

  color: var(--clr-blue);
}

@media screen and (min-width: 600px) {
  .side {
    padding: 32px;
  }
}

.weekly-forecast-wrapper {
  -webkit-margin-before: 2.5rem;
          margin-block-start: 2.5rem;
  padding: 1rem;
  color: var(--clr-blue);
  border-radius: var(--border-radius-secondary);
  background-color: var(--clr-white-a);
}

.section-title {
  text-align: center;
  -webkit-margin-after: 1rem;
          margin-block-end: 1rem;
}

.weekly-forecast {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;

  -webkit-padding-before: 1rem;

          padding-block-start: 1rem;
  border-top: 1px solid var(--clr-neutral);
}

.widget {
  padding: 0.5rem;
  text-align: center;
  border-radius: var(--border-radius-secondary);
  background-color: var(--clr-neutral);
}


.widget-icon {
  width: 100px;
  aspect-ratio: 1 / 1;
  margin-inline: auto;
}

@media screen and (min-width: 600px) {
  .section-title {
    -webkit-margin-after: 0;
            margin-block-end: 0;
  }

  .weekly-forecast {
    justify-content: flex-start;
    grid-gap: 0;
    gap: 0;

    -webkit-padding-before: 0;

            padding-block-start: 0;
  }

  .widget {
    flex: 0 0 4ch;
    margin: 16px 8px;
    padding: 8px 0;
  }

  .widget-icon {
    width: 50px;
  }
}

.loader {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  text-align: center;
  font-size: 1.05rem;
  color: var(--clr-blue);
  background-color: var(--clr-white);
  z-index: 99;
}

.loader > p {
  -webkit-margin-before: 40vh;
          margin-block-start: 40vh;
}
.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  text-align: center;
  color: var(--clr-blue);
  background-color: var(--clr-black-a);
  z-index: 99;
}

.modal-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;

  width: 50%;
  margin: 35vh auto 0 auto;
  padding: 2rem 1rem;
  font-size: 1.05rem;
  border-radius: var(--border-radius-secondary);
  background-color: var(--clr-neutral);
}

@media screen and (min-width: 600px) {
  .modal-content {
    width: 30%;
  }
}
